#game {
  height: 100%;
  display: grid;
  flex-direction: column;
}
#game-container,
#board-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
}
#hand {
  min-width: 320px;
  box-sizing: border-box;
}
#board {
  width: 320px;
  box-sizing: border-box;
  position: relative;
}
#pod-sum {
  position: absolute;
  color: aliceblue;
  width: 45%;
  top: 5%;
  right: 5%;
  font-size: 1.2rem;
  text-align: end;
  word-break: break-word;
}
#result-none {
  position: absolute;
  bottom: 5%;
  right: 5%;
  font-size: 1.4rem;
  color: aliceblue;
}
#actions {
  width: 100%;
  box-sizing: border-box;
}
#action-button-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
