button{
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
  padding: 0;
  appearance: none;
}

/* 参考 https://kouhekikyozou.com/css_close_button */
.batsu {
  display: block;
  position: relative;
  width: 30px;
  height: 30px;
}

.batsu::before, .batsu::after { /* 共通設定 */
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 4px; /* 棒の幅（太さ） */
  height: 25px; /* 棒の高さ */
  background: #282c34;
  border-radius: 2.5px;
}

.batsu::before {
  transform: translate(-50%,-50%) rotate(45deg);
}

.batsu::after {
  transform: translate(-50%,-50%) rotate(-45deg);
}

#close-button {
  text-align: right;
}

