header {
  background-color: #282c34;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  flex-wrap: nowrap;
  padding: 0 16px;
  height: 50px;
  border-bottom: 1px solid #d3d6da;
}
header .title {
  color: aliceblue;
  font-weight: 700;
  font-size: 37px;
  line-height: 100%;
  letter-spacing: 0.01em;
  text-align: center;
  left: 0;
  right: 0;
  pointer-events: none;
}
#App {
  height: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.menu-left,
.menu-right {
  display: flex;
  margin: 0;
  padding: 0;
  align-items: center;
  width: 20%;
  justify-content: flex-start;
}
#nav-button {
  padding-top: 2px;
}
button.icon {
  background: none;
  border: none;
  cursor: pointer;
  padding: 0 4px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.table th {
  vertical-align: middle;
  color: aliceblue;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
