#overlay {
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
#content {
  width: 340px;
  z-index: 2;
  display: flex;
}
.next-game-button {
  width: 170px;
  height: 50px;
  margin: 2% auto;
  text-align: center;
  color: aliceblue;
  border: 2px solid transparent;
  border-color: aliceblue;
  background-color: #282c34;
  font-size: 1.5rem;
  line-height: 1.5;
  border-radius: 0.25rem;
  padding: 0;
}
