#player-button-container,
#card-container {
  display: flex;
  justify-content: center;
  align-items: center;
}
#player-button-container .position {
  text-align: center;
  margin: 2% auto;
  width: 48%;
  color: aliceblue;
  background-color: #282c34;
  border: 1px solid transparent;
  border-color: aliceblue;
  font-size: 1.5rem;
  line-height: 1.5;
  border-radius: 0.25rem;
}
#player-button-container .empty-box {
  width: 23%;
  margin: 2% auto;
}
